.box {
  padding-left: 178px;
}
.iconbtn {
  background: #ffffff;
  color: #788585;
}
.backgroundimage {
  margin: 20px;
  height: 178px;
  width: 95%;
  border-radius: 5px;
}
.grid {
  display: grid;
  margin-top: 5px;
  grid-template-columns: 204px auto 200px;
  max-width: 95%;
  margin-left: 20px;
}
.col1 {
  grid-column: 1;
}
.col2 {
  grid-column: 2;
}
.col3 {
  grid-column: 3;
}

a {
  color: #788585;
}

.contentGrid {
  display: grid;
  margin-top: 5px;
  grid-template-columns: 70px 250px;
  column-gap: 20px;
  row-gap: 10px;
  margin-left: 5px;
  font-family: "Poppins";
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #67748e;
}
.contentcol1 {
  grid-column: 1;
}
.contentcol2 {
  grid-column: 2;
}
.content {
  font-family: "Poppins";
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #67748e;
}
mark {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  letter-spacing: -0.387234px;
  color: #67748e;
  background: none;
}
.convoGrid {
  margin-top: 10px;
  display: grid;
  grid-template-columns: 53px auto 44px;
  gap: 15px 10px;
}
.convocol1 {
  grid-column: 1;
  overflow: hidden;
}
.convocol2 {
  grid-column: 2;
}
.convocol3 {
  grid-column: 3;
}
.displayGrid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 20px;
  max-width: 98%;
}

.profileicons {
  border-radius: 10px;
  width: 50px;
  height: 50px;
}
.reply {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  margin: 10px 0px;
  color: #25b581;
  text-decoration: none;
}
.activitesGrid {
  display: grid;
  grid-template-columns: 750px 500px;
  grid-template-rows: 1fr;
  gap: 20px;
  max-width: 98%;
}
.userTaskGrid {
  display: grid;
  grid-template-columns: 2fr 1fr 0.5fr 0.25fr;
  grid-template-rows: 1fr;
  gap: 10px;
}
.userGridCol1 {
  grid-column: 1;
  align-self: center;
}
.userGridCol2 {
  grid-column: 2;
  align-self: center;
}
.userGridCol3 {
  grid-column: 3;
  align-self: center;
}
.userGridCol4 {
  grid-column: 4;
  align-self: center;
}
.activitiesContent{
  font-family: 'Poppins';
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 21px;

/* first-text-color */

color: #848787;
}

